import React, {useState, useEffect} from 'react';
import {Redirect, Route, Switch} from 'react-router-dom';
import Header from "./components/header";
import Landing from "./pages/landing";
import Login from "./pages/login";
import LayoutChoice from "./pages/layoutChoice";
import Page404 from "./pages/page404";
import "./styles/main.scss";
import PlanChoice from "./pages/planChoice";

export const AuthContext = React.createContext(null);

const App = () => {

    const [isLoggedIn, setLoggedIn] = useState(false);

    return (
        <AuthContext.Provider value={{isLoggedIn, setLoggedIn}} className="App">
          <Header loggedIn={isLoggedIn}/>
          <main>
            <Switch>
                <Route exact path="/" component={Landing}/>
                <Route exact path="/login" component={Login}/>
                <Route exact path="/planchoice" component={PlanChoice}/>
                <Route exact path="/layoutchoice" component={LayoutChoice}/>
                <Route exact path="/404" component={Page404}/>
                <Redirect from="*" to="/404"/>
            </Switch>
          </main>
        </AuthContext.Provider>
    );
};

export default App;
